@use 'base/variables' as v;
@use 'base/mixins' as m;

.clases {
	background-color: v.$gris;
	padding: 5rem 0;
	&__heading {
		@include m.heading;
	}
	&__grid {
		@include m.tablet {
			@include m.grid(2, 2rem);
		}
		@include m.desktop {
			@include m.grid(3, 2rem);
		}
	}
}

.clase {
	border: 1px solid darken(v.$gris, 10%);
	background-color: v.$blanco;

	&__meta {
		background-color: v.$primario;
		display: flex;
		justify-content: space-between;
		padding: 0 1rem;
		color: v.$blanco;
		align-items: center;
	}

	&__precio {
		background-color: v.$secundario;
		padding: 0.5rem;
		font-size: 1.2rem;
		font-weight: 700;
		color: v.$negro;
		border-radius: 1rem;
	}
	&__contenido {
		padding: 2rem;
	}
	&__nombre {
		min-height: 5.5rem;
	}
	&__descripcion {
		color: v.$primario;
		min-height: 5.8rem;
	}
	&__enlace {
		@include m.boton(v.$primario, v.$blanco);
		display: block;
		font-size: 1.6rem;
		padding: 1rem;
	}
}
