@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
	padding: 3rem 0;
	border-bottom: 0.5rem solid v.$primario;
	&__grid {
		@include m.tablet {
			display: flex;
			justify-content: space-between;
		}
	}
	&__copyright {
		text-align: center;
		font-size: 2.4rem;
		margin-top: 3rem;
		@include m.tablet {
			margin: 0;
		}
	}
}
